@import '../../style/variables-mixins.sass'

ul, li
    list-style-type: none
    
.tableau__container  
    width: $full
    padding: 0px 20px 50px 20px
    display: flex
    justify-content: center
    align-items: flex-start
    
    .tableau
        position: relative
        max-width: $full
        width: 90%
        height: auto
        padding: 0px 100px
        display: flex
        flex-direction: row 
        justify-content: center
        align-items: flex-start
        flex-wrap: wrap
       
   
        &__card 
            margin: 10px 
            padding: 40px 40px 0px 40px
            display: flex
            flex-direction: column
            font-family: $f-family
            background-color: #000000
            +border-second-color
            box-shadow: 1px 1px 3px #DEB992
            z-index: 2
        
        .ant-card-head  
            margin: 0
            text-align: center
            font-size: $xl
            font-weight: 400
            color: $primary-color
            text-shadow: 0px 0px 1px $primary-color
    
        ul
            margin: 0
            padding: 0
            +flex-column-center
            font-size: $l
            font-weight: 350
            color: #FFFFFF


//MEDIA QUERIES //

//Exception 
$breakpoint-l: 1105px
@media screen and (max-width: $breakpoint-l)

    .tableau__container 
        width: 90%
        .tableau
            &__card
                padding: 10px 10px 0px 10px

               
// Media Queries MEDIUM DEVICES
$breakpoint-medium: 1024px
@media screen and (max-width: $breakpoint-medium)

    .tableau__container 
        .tableau
            width: $full
            padding: 0
            flex-direction: column
            align-items: center

            &__card
                width: $full
                align-items: center
            ul
                flex-direction: row
                gap: 25px
                font-size: $m  


     
// Media Queries SMALL DEVICES
$breakpoint-small: 768px
@media screen and (max-width: $breakpoint-small)

    .tableau__container 
        .tableau
            &__card
                width: 90%
               
            .ant-card-head  
                font-size: $l
                padding: 0
               
            ul  
                word-wrap: break-word
                flex-direction: column
                text-align: center
    
                font-size: $m  




        

  






   



   


        

